.WriteNowImage {
  display: flex;
  flex-direction: column;
  background-position: center;
  background-image: url("https://user-images.githubusercontent.com/46304321/62010166-b4322600-b156-11e9-90f3-18083e55e959.png");
  background-size: contain;
  background-repeat: no-repeat;
  align-items: center;
  justify-content: center;
  height: 20rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

@media screen and (max-width: 480px) {
  .WriteNowImage {
  height: 15rem;  
  text-align: center;
  margin-bottom: 1rem;
